import {AnalysisResult} from '../actions/TextAnalysisActionTypes'

interface IDefaultState {
    loading: boolean,
    data?: AnalysisResult[]
}

const defaultState : IDefaultState = {
    loading: false
};

const textAnalysisReducer = (state : IDefaultState = defaultState, action : any) : IDefaultState => {
    return state;
};

export default textAnalysisReducer